import * as THREE from 'three';

class CanvasSphere {
  constructor() {
    this.container = document.querySelector('#canvas-block');

    this.scene = null;
    this.camera = null;
    this.renderer = null;
    this._width = null;
    this._height = null;
    this._primitive = null;
    this.geometry = null;
    this.material = null;
    this.mesh = null;
    this.start = Date.now();

    // параметры шейдера
    this.vertexShader = `
    vec3 mod289(vec3 x)
  {
    return x - floor(x * (1.0 / 289.0)) * 289.0;
  }

  vec4 mod289(vec4 x)
  {
    return x - floor(x * (1.0 / 289.0)) * 289.0;
  }

  vec4 permute(vec4 x)
  {
    return mod289(((x*34.0)+1.0)*x);
  }

  vec4 taylorInvSqrt(vec4 r)
  {
    return 1.79284291400159 - 0.85373472095314 * r;
  }

  vec3 fade(vec3 t) {
    return t*t*t*(t*(t*6.0-15.0)+10.0);
  }
  
  // Classic Perlin noise
  float cnoise(vec3 P)
  {
    vec3 Pi0 = floor(P); // Integer part for indexing
    vec3 Pi1 = Pi0 + vec3(1.0); // Integer part + 1
    Pi0 = mod289(Pi0);
    Pi1 = mod289(Pi1);
    vec3 Pf0 = fract(P); // Fractional part for interpolation
    vec3 Pf1 = Pf0 - vec3(1.0); // Fractional part - 1.0
    vec4 ix = vec4(Pi0.x, Pi1.x, Pi0.x, Pi1.x);
    vec4 iy = vec4(Pi0.yy, Pi1.yy);
    vec4 iz0 = Pi0.zzzz;
    vec4 iz1 = Pi1.zzzz;

    vec4 ixy = permute(permute(ix) + iy);
    vec4 ixy0 = permute(ixy + iz0);
    vec4 ixy1 = permute(ixy + iz1);

    vec4 gx0 = ixy0 * (1.0 / 5.0);
    vec4 gy0 = fract(floor(gx0) * (1.0 / 5.0)) - 0.5;
    gx0 = fract(gx0);
    vec4 gz0 = vec4(0.5) - abs(gx0) - abs(gy0);
    vec4 sz0 = step(gz0, vec4(0.0));
    gx0 -= sz0 * (step(0.0, gx0) - 0.5);
    gy0 -= sz0 * (step(0.0, gy0) - 0.5);

    vec4 gx1 = ixy1 * (1.0 / 5.0);
    vec4 gy1 = fract(floor(gx1) * (1.0 / 5.0)) - 0.5;
    gx1 = fract(gx1);
    vec4 gz1 = vec4(0.5) - abs(gx1) - abs(gy1);
    vec4 sz1 = step(gz1, vec4(0.0));
    gx1 -= sz1 * (step(0.0, gx1) - 0.5);
    gy1 -= sz1 * (step(0.0, gy1) - 0.5);

    vec3 g000 = vec3(gx0.x,gy0.x,gz0.x);
    vec3 g100 = vec3(gx0.y,gy0.y,gz0.y);
    vec3 g010 = vec3(gx0.z,gy0.z,gz0.z);
    vec3 g110 = vec3(gx0.w,gy0.w,gz0.w);
    vec3 g001 = vec3(gx1.x,gy1.x,gz1.x);
    vec3 g101 = vec3(gx1.y,gy1.y,gz1.y);
    vec3 g011 = vec3(gx1.z,gy1.z,gz1.z);
    vec3 g111 = vec3(gx1.w,gy1.w,gz1.w);

    vec4 norm0 = taylorInvSqrt(vec4(dot(g000, g000), dot(g010, g010), dot(g100, g100), dot(g110, g110)));
    g000 *= norm0.x;
    g010 *= norm0.y;
    g100 *= norm0.z;
    g110 *= norm0.w;
    vec4 norm1 = taylorInvSqrt(vec4(dot(g001, g001), dot(g011, g011), dot(g101, g101), dot(g111, g111)));
    g001 *= norm1.x;
    g011 *= norm1.y;
    g101 *= norm1.z;
    g111 *= norm1.w;

    float n000 = dot(g000, Pf0);
    float n100 = dot(g100, vec3(Pf1.x, Pf0.yz));
    float n010 = dot(g010, vec3(Pf0.x, Pf1.y, Pf0.z));
    float n110 = dot(g110, vec3(Pf1.xy, Pf0.z));
    float n001 = dot(g001, vec3(Pf0.xy, Pf1.z));
    float n101 = dot(g101, vec3(Pf1.x, Pf0.y, Pf1.z));
    float n011 = dot(g011, vec3(Pf0.x, Pf1.yz));
    float n111 = dot(g111, Pf1);

    vec3 fade_xyz = fade(Pf0);
    vec4 n_z = mix(vec4(n000, n100, n010, n110), vec4(n001, n101, n011, n111), fade_xyz.z);
    vec2 n_yz = mix(n_z.xy, n_z.zw, fade_xyz.y);
    float n_xyz = mix(n_yz.x, n_yz.y, fade_xyz.x);
    return 2.2 * n_xyz;
  }

  // Classic Perlin noise, periodic variant
  float pnoise(vec3 P, vec3 rep)
  {
    vec3 Pi0 = mod(floor(P), rep); // Integer part, modulo period
    vec3 Pi1 = mod(Pi0 + vec3(1.0), rep); // Integer part + 1, mod period
    Pi0 = mod289(Pi0);
    Pi1 = mod289(Pi1);
    vec3 Pf0 = fract(P); // Fractional part for interpolation
    vec3 Pf1 = Pf0 - vec3(1.0); // Fractional part - 1.0
    vec4 ix = vec4(Pi0.x, Pi1.x, Pi0.x, Pi1.x);
    vec4 iy = vec4(Pi0.yy, Pi1.yy);
    vec4 iz0 = Pi0.zzzz;
    vec4 iz1 = Pi1.zzzz;

    vec4 ixy = permute(permute(ix) + iy);
    vec4 ixy0 = permute(ixy + iz0);
    vec4 ixy1 = permute(ixy + iz1);

    vec4 gx0 = ixy0 * (1.0 / 5.0);
    vec4 gy0 = fract(floor(gx0) * (1.0 / 5.0)) - 0.5;
    gx0 = fract(gx0);
    vec4 gz0 = vec4(0.5) - abs(gx0) - abs(gy0);
    vec4 sz0 = step(gz0, vec4(0.0));
    gx0 -= sz0 * (step(0.0, gx0) - 0.5);
    gy0 -= sz0 * (step(0.0, gy0) - 0.5);

    vec4 gx1 = ixy1 * (1.0 / 5.0);
    vec4 gy1 = fract(floor(gx1) * (1.0 / 5.0)) - 0.5;
    gx1 = fract(gx1);
    vec4 gz1 = vec4(0.5) - abs(gx1) - abs(gy1);
    vec4 sz1 = step(gz1, vec4(0.0));
    gx1 -= sz1 * (step(0.0, gx1) - 0.5);
    gy1 -= sz1 * (step(0.0, gy1) - 0.5);

    vec3 g000 = vec3(gx0.x,gy0.x,gz0.x);
    vec3 g100 = vec3(gx0.y,gy0.y,gz0.y);
    vec3 g010 = vec3(gx0.z,gy0.z,gz0.z);
    vec3 g110 = vec3(gx0.w,gy0.w,gz0.w);
    vec3 g001 = vec3(gx1.x,gy1.x,gz1.x);
    vec3 g101 = vec3(gx1.y,gy1.y,gz1.y);
    vec3 g011 = vec3(gx1.z,gy1.z,gz1.z);
    vec3 g111 = vec3(gx1.w,gy1.w,gz1.w);

    vec4 norm0 = taylorInvSqrt(vec4(dot(g000, g000), dot(g010, g010), dot(g100, g100), dot(g110, g110)));
    g000 *= norm0.x;
    g010 *= norm0.y;
    g100 *= norm0.z;
    g110 *= norm0.w;
    vec4 norm1 = taylorInvSqrt(vec4(dot(g001, g001), dot(g011, g011), dot(g101, g101), dot(g111, g111)));
    g001 *= norm1.x;
    g011 *= norm1.y;
    g101 *= norm1.z;
    g111 *= norm1.w;

    float n000 = dot(g000, Pf0);
    float n100 = dot(g100, vec3(Pf1.x, Pf0.yz));
    float n010 = dot(g010, vec3(Pf0.x, Pf1.y, Pf0.z));
    float n110 = dot(g110, vec3(Pf1.xy, Pf0.z));
    float n001 = dot(g001, vec3(Pf0.xy, Pf1.z));
    float n101 = dot(g101, vec3(Pf1.x, Pf0.y, Pf1.z));
    float n011 = dot(g011, vec3(Pf0.x, Pf1.yz));
    float n111 = dot(g111, Pf1);

    vec3 fade_xyz = fade(Pf0);
    vec4 n_z = mix(vec4(n000, n100, n010, n110), vec4(n001, n101, n011, n111), fade_xyz.z);
    vec2 n_yz = mix(n_z.xy, n_z.zw, fade_xyz.y);
    float n_xyz = mix(n_yz.x, n_yz.y, fade_xyz.x);
    return 1.5 * n_xyz;
  }
  
  // Turbulence By Jaume Sanchez => https://codepen.io/spite/
  
  varying vec2 vUv;
  varying vec3 vNormal;
  varying float noise;
  varying float qnoise;
  varying float displacement;
  
  uniform float time;
  uniform float displace;
  uniform float pointscale;
  uniform float decay;
  uniform float size;
  uniform float complex;
  uniform float waves;
  uniform float eqcolor;
  uniform bool fragment;

  float turbulence( vec3 p) {
    float t = - 0.005;
    for (float f = 1.0 ; f <= 1.0 ; f++ ){
      float power = pow( 1.3, f );
      t += abs( pnoise( vec3( power * p ), vec3( 10.0, 10.0, 10.0 ) ) / power );
    }
    return t;
  }
  
  void main() {
    // vNormal = normal;
    vNormal = normalMatrix * normal;
    
    vUv = uv;

    noise = (2.0 *  - waves) * turbulence( decay * abs(normal + time));
    qnoise = (0.2 *  - eqcolor) * turbulence( decay * abs(normal + time));
    float b = pnoise( complex * (position) + vec3( (decay * 2.0) * time ), vec3( 100.0 ) );
    
    displacement = - atan(noise) + tan(b * displace);

    vec3 newPosition = (position) + (normal * displacement);
    gl_Position = (projectionMatrix * modelViewMatrix) * vec4( newPosition, abs(size) );
    gl_PointSize = (3.0);
  }`;
    this.fragmentShader = `
  varying vec3 vNormal;
  varying float qnoise;
  varying float noise;
  
  uniform float time;
  uniform float rcolor;
  uniform float gcolor;
  uniform float bcolor;
  


  void main() {
    float r, g, b;
    
    r = normalize(qnoise + rcolor);
    g = sin(qnoise + gcolor);
    b = sin(qnoise + bcolor);
    
    vec3 light = vec3(-2.0, -1.4, -0.4);
    light = normalize(light);
    float dProd = max(0.0, dot(vNormal, light));

    gl_FragColor = vec4(normalize(dProd + r), sin(dProd + g), sin(dProd + b), 1.0); 
  }`;
    this.uniforms = {
      time: {
        type: 'f',
        value: 0.1,
      },
      pointscale: {
        type: 'f',
        value: 0.2,
      },
      decay: {
        type: 'f',
        value: 0.3,
      },
      size: {
        type: 'f',
        value: 0.3,
      },
      displace: {
        type: 'f',
        value: 0.3,
      },
      complex: {
        type: 'f',
        value: 0.0,
      },
      waves: {
        type: 'f',
        value: 0.1,
      },
      eqcolor: {
        type: 'f',
        value: 0.0,
      },
      rcolor: {
        type: 'f',
        value: 0.0,
      },
      gcolor: {
        type: 'f',
        value: 0.0,
      },
      bcolor: {
        type: 'f',
        value: 0.0,
      },
      fragment: {
        type: 'i',
        value: true,
      },
    };
    this.uniformsMerge = THREE.UniformsUtils.merge([
      THREE.UniformsLib['lights'],
      this.uniforms,
    ]);

    this.options = {
      speed: 0.15,
      size: 0.7,
      perlins: 1.0,
      decay: 3,
      displace: 0.1,
      complex: 1.5,
      waves: 0.12,
      eqcolor: 2, // было 1.3
      rcolor: 1,
      gcolor: 0.32,
      bcolor: 0,
      fragment: true,
      points: false,
    };

    this.init();
  }


  createWorld() {
    this._width = this.container.offsetWidth;
    this._height = this.container.offsetHeight;
    //---
    this.scene = new THREE.Scene();
    //---
    this.camera = new THREE.PerspectiveCamera(35, this._width / this._height, 1, 1000);
    this.camera.position.set(0, 0, 10);

    //---
    this.renderer = new THREE.WebGLRenderer({
      antialias: true,
      alpha: true,
    });
    this.renderer.setSize(this._width, this._height);
    this.renderer.setClearColor(0xffffff, 0);
    this.renderer.shadowMap.enabled = true;


    this.container.appendChild(this.renderer.domElement);
    window.addEventListener('resize', this.onWindowResize.bind(this), false);
  }

  onWindowResize() {
    this._width = this.container.offsetWidth;
    this._height = this.container.offsetHeight;
    this.renderer.setSize(this._width, this._height);
    this.camera.aspect = this._width / this._height;
    this.camera.updateProjectionMatrix();
    console.log('- resize -');
  }

  createMesh() {
    this.material = new THREE.ShaderMaterial({
      uniforms: this.uniformsMerge,
      vertexShader: this.vertexShader,
      fragmentShader: this.fragmentShader,
      lights: true,
    });
    this.geometry = new THREE.IcosahedronBufferGeometry(2.15, 6);
    this.mesh = new THREE.Mesh(this.geometry, this.material);
    this.scene.add(this.mesh)
  }

  animation(time) {
    time *= 0.0002;

    this.mesh.rotation.x = time;
    this.mesh.rotation.y = time;

    this.material.uniforms['time'].value =
      (this.options.speed / 1000) * (Date.now() - this.start);

    this.material.uniforms['pointscale'].value = this.options.perlins;
    this.material.uniforms['decay'].value = this.options.decay;
    this.material.uniforms['size'].value = this.options.size;
    this.material.uniforms['displace'].value = this.options.displace;
    this.material.uniforms['complex'].value = this.options.complex;
    this.material.uniforms['waves'].value = this.options.waves;
    this.material.uniforms['fragment'].value = this.options.fragment;

    this.material.uniforms['eqcolor'].value = this.options.eqcolor;
    this.material.uniforms['rcolor'].value = this.options.rcolor;
    this.material.uniforms['gcolor'].value = this.options.gcolor;
    this.material.uniforms['bcolor'].value = this.options.bcolor;

    requestAnimationFrame(this.animation.bind(this));
    this.renderer.render(this.scene, this.camera);
  }

  init() {
    this.createWorld();
    this.createMesh();
    this.animation();
  }
}

export default CanvasSphere;